.card-spacing {
  margin: 5% 30px !important;
}
.card-content {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.card-heading {
  font-size: 25px;
  font-weight: 500;
}
.icon-size {
  height: 70px;
  width: 70px;
}
.icon-style {
  width: 100% !important;
  height: 100%;
  color: white;
}
.card-color:hover {
  background-color: crimson !important;
  color: white !important;
}
.imageContner-project {
  height: 150px;
  width: 150px;
}
.image-size-project {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 100%;
  border: 6px solid crimson;
}
